import React from "react"
import "./contact.css"
import {AiOutlineMail} from 'react-icons/ai';
import {BsLinkedin} from 'react-icons/bs';
import emailjs from 'emailjs-com';
import { useRef } from 'react';

const Contact =() => {
    const form = useRef();
    const sendEmail = (e) => {
        e.preventDefault();
    
        emailjs.sendForm('service_597q0gz', 'template_hbu6ame', form.current, 'ROze_HC55hSUECVRm')
          .then((result) => {
              console.log(result.text);
          }, (error) => {
              console.log(error.text);
          });
          e.target.reset();
      };


    return(
        <section id="contact">
            <h5>Get In Touch</h5>
            <h2>Contact Me</h2>
            <div className="container contact_container">
                <div className="contact_options">
                    <article className="contact_option">
                        <AiOutlineMail className="contact_option-icon"/>
                        <h4>Email</h4>
                        <h5>anisharachel2002@gmail.com</h5>
                        <a href="mailto:anisharachel2002@gmail.com">Send a message</a>
                    </article>
                    <article className="contact_option">
                        <BsLinkedin className="contact_option-icon"/>
                        <h4>Linkedin</h4>
                        <h5>Connect on Linkedin</h5>
                        <a href="https://www.linkedin.com/in/anisha-rachel-george-467781212/" target='_blank'>Send a message</a>
                    </article>
                </div>
                <form ref={form} onSubmit={sendEmail}>
                    <input type="text" name="name" placeholder="Your Full Name" required/>
                    <input type="email" name="email" placeholder="Your Email" required/>
                    <textarea name="message" rows="7" placeholder="Your Message" required></textarea>
                    <button type="submit" className="btn btn-primary">Send Message</button>
                </form>
            </div>
        </section>
    )
}

export default Contact;